.header {
  flex: 0 0 auto;
  display: flex;
  justify-content: space-between;
  padding-top: 45px;
  margin-bottom: 40px;
  min-height: 74px;
  border-bottom: 1px solid rgba(84, 84, 84, .7);
}

@media screen and (max-width: 400px) {
  .header {
    margin-top: 28px;
    margin-bottom: 41px;
    min-height: 56px;
  }
}
