.profile__name {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  grid-area: name;
  padding: 0;
  margin: 0;
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  line-height: 48px;
}

@media screen and (max-width: 400px) {
  .profile__name {
    margin-left: 27px;
    font-size: 27px;
    line-height: 33px;
  }
}
